import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import {selectFriends} from '../../store/reducers';
import {frendsActions} from '../../store/actions';

import {Panel,Search} from '@vkontakte/vkui';

import User from '../../components/User';
import RefreshList from '../../components/RefreshList';
import HeaderPanel from '../../components/HeaderPanel';

const Friends = ({id, go, users, dispatch, connections}) => {
	let offsetFriends = 100;
	const [isFetching, setIsFetching] = useState(false);
	const [search, setSearch] = useState('');
	const [userSearch, setUserSearch] = useState(users);
	const {updateFriends, fetchedFriends} = frendsActions;

	useEffect(() => {
		window.addEventListener('scroll', handleScroll);
		dispatch(fetchedFriends(setFetching));
		setUserSearch(users);
	}, []);

	const onRefresh = () => {
		setIsFetching(true);
		dispatch(fetchedFriends(setFetching));
		setUserSearch(users);
	};

	const setFetching = () => setIsFetching(false);

	const handleScroll = (e) => {
		const scrollHeight = e.target.documentElement.scrollHeight;
		const clientHeight = e.target.documentElement.clientHeight;
		const scrollY = window.pageYOffset + 5;
		if (scrollY >= scrollHeight - clientHeight) {
			dispatch(updateFriends(offsetFriends));
			setUserSearch(users);
			offsetFriends += 100;
		}
	}

	const handleSearch = (search) => {
		setSearch(search);
		const searchUsers = users.filter(({last_name}) => last_name.toLowerCase().indexOf(search.toLowerCase()) > -1);
		setUserSearch(searchUsers);
	}

	return(
		<Panel id={id}>
			<HeaderPanel title='Друзья' handelClick={go} dataTo='home' />
			<Search value={search} onChange={handleSearch}/>
			<RefreshList
				handleScroll={handleScroll}
				handleRefresh={onRefresh}
				isFetching={isFetching}
				list={userSearch}
				footerNameCount='друзей'
			>
				<User size={56} go={go} />
			</RefreshList>
		</Panel>
	);
}

Friends.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	connections: PropTypes.string
};

const mapStateToProps = (state) => ({
	users: selectFriends.getFriends(state)
});
export default connect(mapStateToProps, null)(Friends);