import React from 'react';
import PropTypes from 'prop-types';
import {Cell, Avatar} from '@vkontakte/vkui';

const INSTAGRAM = 'instagram';
const VK = 'vk';

const connectionsType = ({id, type, instagram}) => {
	switch(type) {
		case VK: return `https://vk.com/id${id}`;
		case INSTAGRAM: return `https://www.instagram.com/${instagram}`;
		default: return `https://vk.com/id${id}`;
	}
};

const  User = ({
	size=56,
	id,
	photo_50,
	photo_100,
	photo_200,
	photo_200_orig,
	city = {title: ''},
	first_name,
	last_name,
	bdate,
	instagram,
	connections
}) => {
	return (
		<Cell
			key={id}
			before={<Avatar
					size={size}
					src={photo_200_orig || photo_200 || photo_100 || photo_50}
					onClick={() => window.location = connectionsType({ id, type: connections, instagram })}
				/>}
			size="l"
			description={city.title}
			bottomContent={bdate && `День рождения: ${bdate}`}
		>
			{`${first_name} ${last_name}`}
		</Cell>
	);
}

User.propTypes = {
	id: PropTypes.number,
	size: PropTypes.number,
	photo_50: PropTypes.string,
	photo_100: PropTypes.string,
	photo_200: PropTypes.string,
	photo_200_orig: PropTypes.string,
	first_name: PropTypes.string,
	last_name: PropTypes.string,
	bdate: PropTypes.string,
	connections: PropTypes.string,
	instagram: PropTypes.string,
	city: PropTypes.shape({
		title: PropTypes.string,
	})
};

export default User;